// import { apiTeamList, apiUserInfo } from '@/service/index'
export default {
	data() {
		return {
			teamList: [],
			detail: {}
		}
	},
	onShow() {
		// this.getUserInfo()
		// this.getTeamList()
	},
	methods: {
		getUserInfo() {
			apiUserInfo()
				.then((res) => {
					if (res.code == 0) {
						this.detail = res.data
					}
				})
		},
		getTeamList() {
			apiTeamList()
				.then((res) => {
					if (res.code == 0) {
						this.teamList = res.data
					}
				})
		}
	}
}
